import { OnInit, Input, Component, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormGroupDirective, NgForm, Validators, FormBuilder, FormControl } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { User } from '../models/User.model';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const invalidCtrl = !!(control && control.invalid && control.parent.dirty);
    const invalidParent = !!(control && control.parent && control.parent.invalid && control.parent.dirty);

    return (invalidCtrl || invalidParent);
  }
}

@Component({
  selector: 'registration-form',
  templateUrl: './registration-form.component.html',
  styleUrls: ['./registration-form.component.css']
})
export class RegistrationFormComponent implements OnInit {
  public form: FormGroup;

  matcher = new MyErrorStateMatcher();

  constructor(private formBuilder: FormBuilder) {
    this.form = this.formBuilder.group({
      username: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email]),
      dateOfBirth: new FormControl(new Date()),
      password: new FormControl('', Validators.required),
      passwordCheck: new FormControl('', Validators.required)
    }, { validator: this.checkPasswords });

  }

  ngOnInit() {
    this.form = new FormGroup({
      username: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email]),
      dateOfBirth: new FormControl(new Date(), Validators.required),
      password: new FormControl('', Validators.required),
      passwordCheck: new FormControl('', Validators.required)
    });
  }

  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let pass = group.controls.password.value;
    let confirmPass = group.controls.confirmPass.value;

    return pass === confirmPass ? null : { notSame: true }
  }

  // public hasError = (controlName: string, errorName: string) => {
  //   return this.form.controls[controlName].hasError(errorName);
  // }

  submit() {
    if (this.form.valid) {
      let user: User = {
        UserName: this.form.controls['username'].value,
        Password: this.form.controls['password'].value,
        Email: this.form.controls['email'].value,
        DateOfBirth: this.form.controls['dateOfBirth'].value,
        Residency: this.form.controls['residency'].value
      };

      this.submitEM.emit(user);
    }
    else {
    }
  }

  onBlurMethod(value) {
    if (value != this.form.controls['password'].value) {

    }
    console.log(value);
    console.log(this.form.valid);
  }

  //@Input() error: string | null;

  @Output() submitEM = new EventEmitter();
}